@import url('https://fonts.googleapis.com/css?family=Raleway:300,400,500,700&display=swap');
:root{
--sidebar-flex:1.5;
--condiv-flex:6;
--fontsize-p:1.2rem;
--fontsize-nav:1.6rem;
--typing-font:3rem;
}
*
{
margin:0;
padding: 0, 0, 0, 30px;
}

body
{
font-family: 'Raleway';
}

.App
{
display: flex;
width: 100vw;
min-height: 100vh;
}
/* .condiv class is for the right side content container*/

.condiv
{
padding: 50px ;
background-color: #f4f5f9;
flex:var(--condiv-flex);
}

/*flex:var(--sidebar-flex);*/
nav
{
padding:60px 20px 0px 20px ;
flex:var(--sidebar-flex);
display: flex;
background: rgb(238,103,143);
background: linear-gradient(180deg, rgba(238,103,143,1) 50%, rgba(132,124,252,1) 80%);
}

nav ul
{
  font-size:var(--fontsize-nav) ;
  text-align: center;
  list-style-type: none;
  text-decoration: none;
  color: white !important;
  position: relative;
  }
nav ul li
{
  margin:30px;
}

.active
{
font-weight: bolder;
}

.social
{
position: absolute;
margin-top: 20px;
display: flex;
bottom: 60px;
}

.social i
{
padding-right: 30px;
font-size: 25px;
color: #9a9b9c;
}

.social i:hover
{
color: #ffffff;
}

.subtopic
{
margin:10px 10px 30px 10px;
}

p
{
font-size: 15px;
text-align: justify;
}

b{
  font-size: 25px;
  text-align: justify;
}


h1,h2,h3,h4,p
{
margin: 10px, 10px, 30px, 10px;
}

.widecard
  {
  border:0px solid #9a9b9c;
  display: flex;
  margin:10px 10px 30px 10px;
  padding: 5px 5px 5px 5px;
  width: 340px;
  height: 110px;
  text-align: justify;
  border-radius: 5px;
  border-style: solid;
  border-color: black;
  border-width: 1px;
  }


.widecard:hover
  {
  color: #1b1d20;
  background-color: #ffffff;
  box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
  }

  .project {
    display: inline-block;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    padding:0px 4px;
  }

  .project-items {
    display: inline-block;
    border: #000000;
    border-style: solid;
    border-width: 1px;
    border-radius: 5px;
    padding: 5px 5px 5px 5px;
    margin-right:10px;
    margin-top: 10px;
    width: auto;
    height: auto;    
  }

  .project-items:hover
  {
  color: #1b1d20;
  background-color: #ffffff;
  box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
  }

  .profilepic {
    vertical-align: middle;
    width: 230px;
    height: 252px;
    border-radius: 50%;
    margin-left: auto;
    margin-right: auto;  
  }

  .nav-links:hover{
    background: black;
  }

  .icons{
    position: absolute;  
    padding-top: 20px;

  }

  .contact{
    display: inline-block;
    padding-top: 30px;
  }

  .resume{
    padding-top: 30px;
  }
